body{
  background-image: url('https://img.freepik.com/free-photo/3d-daily-planner-calendar-pencil-alarm-clock_107791-15857.jpg?size=626&ext=jpg&ga=GA1.2.842900672.1689959330&semt=sph');
  background-size: cover;
 
