.home-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .home-container h2 {
    padding: 5px 0px;
    margin-top: -3px;
    font-size: 22px;
    margin-bottom: 20px;
    text-decoration: underline;
    text-align: center;
    box-shadow:5px 10px 10px rgb(194, 127, 194);
    /* line-height: 30px; */
  }
  
  .Task-list {
    list-style: none;
    padding: 0;
  }
  
  .Task-item {
    color: white;
    border: 1px solid #f70606;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    background-color: grey;
    box-shadow: 5px 10px 10px #0b0b0b;
    position: relative;
  }
  
  .Task-item span {
    display: block;
    margin-bottom: 5px;
  }
  
  .no-Tasks {
    text-align: center;
    font-size: 18px;
    color: grey;
    margin-top: 20px;
  }

  .delbtn{
    position: absolute;
    top: 55px;
 right: 20px;
    background-color: red;
    color: white;
   padding: 2px 20px;
   border-radius: 5px;

  }

  .delbtn:hover{
    background-color: rgb(167, 3, 3);
    color: white;
    cursor: pointer;
 
  }
  